@import '../../styles/fonts';

.hidden {
    display: none;
}

.hover_font{
    text-decoration-line: underline;
    text-decoration-color: #4095A2;
    color: #4095A2;
    text-decoration-thickness: 2px;
    text-underline-position: under;
}

// * 1. 전체 컨테이너
.topbar_container {
    z-index: 100;
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100%;
    
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    background-color: #F8F8F8;
    color: #000000;
    font-family: 'Pretendard-Medium';
    box-sizing: border-box;
    padding: 0;
    height: 6.25rem;
    // column-gap: 30px;

    .navbar_conainer{
        width: 100%;
        height: 100%;
        // background-color: #ff5353;
        max-width: var(--desktop-width);  // 최대 너비 설정
        display: flex;
        box-sizing: border-box;
        flex-direction: row;
        align-items: center;
        justify-content: space-between; // 아이템 동일한 간격
        
        // * 로고 이미지 컨테이너
        .logo_container {
            > img {
                width: 11.25rem; //180px
                height: 4rem; // 64px
                cursor: pointer;
            }
        }

        // * 2. 메인 메뉴 컨테이너
        .main_container {
            display: flex;
            box-sizing: border-box;
            min-width: 50%;
            height: 100%;
            
            
            // 메뉴 ul 컨테이너
            .menu_all_container {
                margin: 0px;
                width: 100%;
                height: 100%;
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: space-around; // 아이템 동일한 간격
                list-style-type: none; // 점 삭제
                position: relative;

                // 장례식장 소개, 시설현황, 이용안내 진행절차
                .menus_container {
                    width: 100%;
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    // 타이틀
                    .menu_title {
                        font-weight: bold;
                        cursor: pointer;
                        font-size: 1rem;
                        padding: 0.5rem 0;
                        
                        >a{
                            color: inherit; // 링크 색상 제거
                            text-decoration:none; // 밑줄 삭제
                        }
                        // 폰트 색상 변경, 밑줄
                        &:hover {
                            text-decoration-line: underline;
                            text-decoration-color: #4095A2;
                            color: #4095A2;
                            text-decoration-thickness: 2px;
                            text-underline-position: under;
                        }

                    }

                    // menu_title에 hover 시 sub_menus가 보이게 설정
                    &:hover > .sub_menus {
                        display: flex;
                        justify-content: center;
                        flex-direction: row;
                        align-items: stretch;
                        column-gap: 10%;
                    }


                    

                    // * 서브 메뉴
                    .sub_menus {
                        display: none;
                        position: fixed;
                        top: 6.25rem;
                        left: 0px;
                        width: 100%;
                        background-color: #FFFFFF;
                        list-style: none;
                        padding: 2.5rem; //40px
                        min-width: 200px;

                        box-sizing: border-box;

                        border: 1px solid #D0D0D0;

                        .sub_sub_menus {
                            display: flex;
                            flex-direction: column;
                            text-align: left;
                            border-left: 1px solid #D0D0D0;
                            height: 100%;
                            box-sizing: border-box;
                            padding-left: 20px;
                            text-align: left;

                            // align-items: center;
                            // justify-content: center;

                            //현재 선택자의 마지막 자식 요소를 선택
                            &:last-child {
                                margin-bottom: 0;
                            }

                            .sub_menu_title {
                                color: #000000;
                                text-decoration:none; // 밑줄 삭제
                                font-weight: bold;
                                margin: 0 0;
                                margin-bottom: 2.1875rem; //35px
                                // margin: 0.5rem 0px; 

                                // &:hover{
                                //     color: #4095A2;
                                // }
                            }

                            .sub_menu_items {
                                display: flex;
                                flex-direction: column;
                                row-gap: 20px;
                                margin: 0;
                                padding: 0;
                                list-style: none;
                                text-align: left;
                                justify-content: center;

                                .sub_menu_item {
                                    display: flex;
                                    flex-direction: column;
                                    text-align: left;
                                    justify-content: center;
                                    margin-bottom: 0 0;
                                    
                                    >a {
                                        text-decoration:none; // 밑줄 삭제
                                        color: #767676;

                                        &:hover{
                                            color: #4095A2;
                                        }
                                    }

                                    &:last-child {
                                        margin-bottom: 0;
                                    }

                                    
                                    cursor: pointer;
                                }
                            }
                        }
                    }
                }
            }

            // * 3. 메뉴 더 보기 컨테이너
            .add_menu_container {
                > img {
                    // margin-left: auto;
                    // padding-left: auto;
                    width: 1.5rem;
                    height: 1.5rem;
                    cursor: pointer;
                }
            }
        }
    }

}
