.subTitle{
  color: #4095A2;
  font-family: 'Pretendard-SemiBold';
  font-size: 20px;
  margin-bottom: 10px;
  font-weight: 600;
}

.mainTitle{
  font-family: 'Pretendard-Bold';
  font-size: 24px;
  margin-bottom: 20px;
  font-weight: 700;
}


// 슬라이드 화살표 색상 변경
.slick-prev:before, .slick-next:before {
  font-family: 'Pretendard-Medium';
  font-size: 30px;
  color: #D0D0D0;
  line-height: 30px;
}

.facility-carousel {
  width: 100%;
  margin: auto;
  justify-content: space-between;
  margin-bottom: 50px;
}

.facilityItem {
  position: relative;
  flex-direction: column;
  align-items: flex-start;
  display: flex;
  width: 300px;
  
  .title {
    position: absolute;
    bottom: 15px;
    right: 15px;
    color: #fff;
    font-size: 1em;
    padding: 0px 5px;
    border-radius: 4px;
    text-align: left;
    transition: color 0.3s ease;
  }
  &:hover .title {
    color: #fff;
  }
}

.facilityImage {
  width: 300px;
  height: 188px;
  height: auto;
  border-radius: 1px;
  transition: transform 0.3s ease;
}

.slick-slide {
  display: flex !important;
  justify-content: center;
}

.slick-slide img {
  display: block;
}
