

// 예약전화 : 052)264-1017
@mixin callout{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    padding: 24px 0;
    gap: 15px;
    border: 1px solid #4095A2;
    color: #4095A2;
    font-family: "Pretendard-Medium";
    font-size: 18px;
}

//믹스인 사용법 : @include resuable-style;

// > 30만상
@mixin subheading {
    font-family: "Pretendard-SemiBold";
    color: #000;
    font-size: 20px;
    margin: 0;
    
    &::before{
        content: "▶ ";
    }
}
